import React, { useState } from 'react';
import { LoginPage } from './pages/login';
import SignUpPage from './pages/signup/SignUp';
import { styled } from '@mui/system';
import useMediaQuery from './services/useMediaQuery';
import { ForgotPassword } from './pages/forgot-password';
import VerifyModal from './pages/signup/components/VerifyModal';

interface ComponentProps {
  isSlideIn: boolean;
}

type PageView = 'login' | 'signup' | 'forgotPassword';

const Component = styled('div')<ComponentProps>`
  width: ${props => (props.isSlideIn ? '100%' : '0')};
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: ${props => (props.isSlideIn ? 'visible' : 'hidden')};
  transition: opacity 0.3s ease, transform 0.3s ease;
  transform: ${props => (props.isSlideIn ? 'translateX(0)' : 'translateX(-100%)')};
`;

function App() {
  const { isMobile } = useMediaQuery();
  const [pageView, setPageView] = useState<PageView>('login');

  const changeToSignUp = () => {
    setPageView('signup');
  };

  const changeToLogin = () => {
    setPageView('login');
  };

  const changeToForgot = () => {
    setPageView('forgotPassword');
  };

  const [openNoti, setOpenNoti] = React.useState<string>();

  const onSuccessSignUp = () => {
    setOpenNoti('Remember to verify your email. Check your inbox or spam');
  };

  const onSuccessForgot = () => {
    setOpenNoti("We've just sent you an email to reset your password.");
  };

  const handleClose = (event?: React.SyntheticEvent | Event, reason?: string) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpenNoti(undefined);
    if (isMobile) {
      window.postMessage('Close event');
    }
  };

  return (
    <>
      {isMobile && (
        <>
          <Component isSlideIn={pageView === 'signup'}>
            <SignUpPage toLogin={changeToLogin} onSuccess={onSuccessSignUp} />
          </Component>
        </>
      )}
      <Component isSlideIn={pageView === 'forgotPassword'}>
        <ForgotPassword toLogin={changeToLogin} onSuccess={onSuccessForgot} />
      </Component>
      <Component isSlideIn={pageView === 'login'}>
        <LoginPage toSignUp={changeToSignUp} toForgot={changeToForgot} />
      </Component>
      <VerifyModal open={!!openNoti} handleClose={handleClose} des={openNoti} />
    </>
  );
}

export default App;
