import React, { useMemo, useState } from 'react';
import { ReactComponent as AppleSocial } from '@/assets/apple-social.svg';
import { ReactComponent as FacebookSocial } from '@/assets/facebook-social.svg';
import { ReactComponent as GoogleSocial } from '@/assets/google-social.svg';
import { Box, FormHelperText, Grid, IconButton, Paper, Typography } from '@mui/material';
import useMediaQuery from '@/services/useMediaQuery';
import { LoginForm } from './components/LoginForm';
import { LoginRequest } from '@/@types';
import webAuth, { config } from '@/config/auth0.config';

interface LoginPageProps {
  toSignUp: () => void;
  toForgot: () => void;
}

function LoginPage({ toSignUp, toForgot }: LoginPageProps) {
  const [loading, setLoading] = useState(false);
  const { isMobile } = useMediaQuery();

  const [error, setError] = useState<string>();

  const handleLogin = (data: LoginRequest) => {
    setLoading(true);
    const databaseConnection = 'Username-Password-Authentication';

    webAuth?.login(
      {
        realm: databaseConnection,
        email: data.email,
        password: data.password,
      },
      error => {
        setLoading(false);
        if (error) handleError(error);
      }
    );
  };

  const handleError = (error?: auth0.Auth0Error | string) => {
    if (typeof error === 'string') {
      setError(error);
    } else {
      if (typeof error?.description === 'string') {
        setError(error.description);
      } else {
        setError(error?.policy || 'Something went wrong. Please try again later!');
      }
    }
  };

  const errorsFormatted = useMemo(() => {
    const errors = error?.split('\n');
    return errors?.map(e => <FormHelperText error>{e}</FormHelperText>) || error;
  }, [error]);

  const socialLogin = (social: 'facebook' | 'google' | 'apple') => {
    let connection;
    switch (social) {
      case 'google':
        connection = 'google-oauth2';
        break;
      case 'facebook':
        connection = 'facebook';
        break;
      case 'apple':
        connection = 'apple';
        break;
      default:
        break;
    }
    try {
      webAuth?.authorize({
        connection,
        domain: config.auth0Domain,
        clientID: config.clientID,
        redirectUri: config.callbackURL,
        responseType: 'token id_token',
      });
    } catch (error) {
      handleError('Something went wrong. Please try again later!');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Box display="flex" justifyContent="center" alignItems="center" height="100vh">
      <Paper
        elevation={3}
        sx={{
          borderRadius: isMobile ? 0 : 4,
          width: '100%',
          maxWidth: 496,
          height: isMobile ? '100vh' : 'unset',
          display: isMobile ? 'flex' : 'block',
          flexDirection: 'column',
          gap: isMobile ? 5 : 0,
        }}
      >
        <Box>
          <Box
            sx={{
              maxWidth: '100%',
              height: 'auto',
              backgroundColor: '#DFB424',
              borderRadius: 4,
              borderTopLeftRadius: isMobile ? 0 : 16,
              borderTopRightRadius: isMobile ? 0 : 16,
            }}
          >
            <img
              src="https://raw.githubusercontent.com/viktor1910/login-form/main/public/singin-banner.png"
              alt="login-banner"
              style={{ width: '100%', height: 'auto', display: 'block' }}
            />
          </Box>
          <Box
            sx={{
              px: isMobile ? 4 : 10,
              pt: isMobile ? 6 : 10,
              pb: 6,
            }}
          >
            {error && <Box pb={4}>{errorsFormatted}</Box>}
            <LoginForm onLogin={handleLogin} toSignUp={toSignUp} isLoading={loading} toForgot={toForgot} />
          </Box>
        </Box>

        <Box marginBottom={4}>
          <Typography variant="body2" color="#7D848D" textAlign="center" lineHeight="14px">
            Or sign in with
          </Typography>
          <Grid
            container
            justifyContent={isMobile ? 'space-between' : 'center'}
            width={200}
            mx="auto"
            wrap="nowrap"
            mt={4}
          >
            {isMobile && (
              <Grid item pt={0}>
                <IconButton
                  onClick={() => socialLogin('facebook')}
                  sx={{
                    padding: 0,
                  }}
                >
                  <FacebookSocial />
                </IconButton>
              </Grid>
            )}

            <Grid item pt={0}>
              <IconButton
                onClick={() => socialLogin('google')}
                sx={{
                  padding: 0,
                }}
              >
                <GoogleSocial />
              </IconButton>
            </Grid>
            {isMobile && (
              <Grid item pt={0}>
                <IconButton
                  onClick={() => socialLogin('apple')}
                  sx={{
                    padding: 0,
                  }}
                >
                  <AppleSocial />
                </IconButton>
              </Grid>
            )}
          </Grid>
        </Box>
      </Paper>
    </Box>
  );
}

export default LoginPage;
